<template>
  <v-container style="position: relative">
    <v-row>
      <v-col cols="12">
        Please input your private ticket:
      </v-col>
      <v-col cols="12">
        <v-text-field
            ref="query"
            outlined
            dense
            hide-details="auto"
            v-model="ticket"
            :rules="[
                () => /[0-9a-zA-Z][0-9a-zA-Z][0-9a-zA-Z][0-9a-zA-Z][0-9a-zA-Z][0-9a-zA-Z]/.test(ticket) || 'Ticket pattern error!',
                () => !!ticket || 'Required!'
                ]"
        ></v-text-field>
      </v-col>
      <v-col cols="12" class="text-center">
        <v-btn color="primary" @click="queryReport">Query Report</v-btn>
      </v-col>
    </v-row>

    <div class="toast" v-show="toastShow">
      {{ toastText }}
    </div>

    <v-overlay :value="overlay">
      <v-progress-circular
          indeterminate
          size="64"
      ></v-progress-circular>
    </v-overlay>
  </v-container>
</template>

<script>
// import 'vue-toast/dist/vue-toast.min.css'
// import VueToast from 'vue-toast'
// import { Toast } from 'mint-ui';
// import 'mint-ui/'
// import { mdiStar } from '@mdi/js'

export default {
  name: "Query",

  // components: { VueToast },

  data: () => ({

    echart: null,

    toastShow: false,
    toastText: '',
    overlay: false,
    ticket: '',
    src: '',

    name: '',
    addr: '',
    time: '',
  }),

  mounted () {
  },

  methods: {
    toast (text) {
      let v = this
      v.toastText = text
      v.toastShow = true
      setTimeout(function () {
        v.toastShow = false
      }, 1500)
    },

    queryReport () {
      if (!this.$refs.query.validate()) {
        return
      }
      this.overlay = true
      this.$ajax('get', '/bpi/query', { ticket: this.ticket })
          .then(r => {
            this.overlay = false
            const data = r.data.data
            console.log(data)
            if (data === null) {
              this.toast('Ticket not found!')
            } else {
              let cont = false
              if (data.result != null) {
                const rsb = data.result.split(/,|，/)
                // console.log(rsb)
                if (rsb.length === 6) {
                  cont = true
                }
              }
              if (!cont) {
                this.toast('Wait a moment!')
              } else {
                this.$router.push({
                  path: '/report',
                  query: {
                    item: JSON.stringify(data)
                  }
                })
              }
            }
          })
          .catch(e => {
            this.overlay = false
            console.log(e)
            this.toast('Error get ticket')
          })
    }
  },
}
</script>

<style scoped>
.toast {
  position: fixed;
  z-index: 2000;
  left: 50%;
  top:45%;
  transition:all .5s;
  -webkit-transform: translateX(-50%) translateY(-50%);
  -moz-transform: translateX(-50%) translateY(-50%);
  -ms-transform: translateX(-50%) translateY(-50%);
  -o-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
  text-align: center;
  border-radius: 5px;
  color:#FFF;
  background: rgba(17, 17, 17, 0.7);
  height: 45px;
  line-height: 45px;
  padding: 0 15px;
  max-width: 150px;
}
</style>