import Vue from 'vue'
import App from './App.vue'
import router from './router'
import vuetify from './plugins/vuetify';
import ImageUploader from "vue-image-upload-resize";
import echarts from 'echarts'
import axios from "axios";
import qs from "qs";

Vue.config.productionTip = false

Vue.prototype.$echarts = echarts

Vue.use(ImageUploader)

// axios begin

Vue.prototype.BASE_URL = '/api/'  // 'http://localhost:9000/'
Vue.prototype.$http = axios.create({
  baseURL: '/api/'  // 'http://localhost:9000/'
})

const CancelToken = axios.CancelToken

Vue.$httpRequestList = []

Vue.prototype.$ajax = (type, url, data) => {
  return new Promise((resolve, reject) => {  // 封装ajax
    let aa = {
      method: type,
      url: url,
      cancelToken: new CancelToken(c => {  // 强行中断请求要用到的
        Vue.$httpRequestList.push(c)
      }),
      paramsSerializer: (params) => qs.stringify(params, { indices: false })
    }
    let json = (type === 'get') ? Object.assign(aa, { params: data }) : Object.assign(aa, { data: data })
    return Vue.prototype.$http(json).then(res => {
      resolve(res)
    }).catch(error => {  // 中断请求和请求出错的处理
      if (error.message === "interrupt") {
        console.log('已中断请求')
      } else {
        reject(error)
      }
    })
  })
}

Vue.prototype.$post = (url, data) => {
  return new Promise((resolve, reject) => {  // 封装ajax
    let json = {
      method: 'post',
      url: url,
      cancelToken: new CancelToken(c => {  // 强行中断请求要用到的
        Vue.$httpRequestList.push(c)
      }),
      headers: { 'Content-Type': 'multipart/form-data' },
      data: data
    }
    return Vue.prototype.$http(json).then(res => {
      resolve(res)
    }).catch(error => {  // 中断请求和请求出错的处理
      if (error.message === "interrupt") {
        console.log('已中断请求')
      } else {
        reject(error)
      }
    })
  })
}

// axios end

new Vue({
  vuetify,
  router,
  render: h => h(App)
}).$mount('#app')
