<script>
import { mdiCamera, mdiCheck } from "@mdi/js";

export default {
  name: 'HelloWorld',

  data: () => ({
    mdiCamera, mdiCheck,

    step: 1,
    stepIn: 1,
    stepInside1: 1,
    stepInside2: true,

    // Page I
    name: '',
    password: '',

    // Page II

    crop_type: '',
    seeding_time: '',
    cultivated_area: '',
    sampling_time: '',
    sampling_location: '',
    sampling_person: '',

    // Page V

    hasImage1: false,
    image1: null,
    hasImage2: false,
    image2: null,

    // Page VI

    edit_p_striiformis: false,
    p_striiformis: 0.83,
    edit_p_graminis: false,
    p_graminis: 0.04,
    edit_p_triticina: false,
    p_triticina: 0.01,
    edit_b_graminis: false,
    b_graminis: 0,
    edit_f_graminearum: false,
    f_graminearum: 0,
    edit_r_solani: false,
    r_solani: 0.02,

    // Page VII

    echart: null,
    edit_disease_type: false,
    disease_type: 'Stripe rust',
    disease_types: ['Stripe rust', 'Stem rust', 'Leaf rust', 'Powdery mildew', 'Fusarium head blight', 'Sheath blight'],

    // Page VIII

    fungicides: {
      'Stripe rust': [
          [1],
          ['Spraying on leaves'],
          ['15% triadimefon', '135-180 g/ha'],
          ['30% hexaconazole', '36-54 g/ha']
      ],
      'Stem rust': [
          [1, 3],
          ['Spraying on wheat'],
          ['triadimefon', '150-300 g/ha'],
          ['Seed dressing'],
          ['triadimefon', '30 g/100 kg']
      ],
      'Leaf rust': [
          [1, 3],
          ['Seed dressing'],
          ['Flutriafol in-furrow', '400 mL/ha'],
          ['Spraying on wheat'],
          ['epoxiconazole', '135 g/ha']
      ],
      'Powdery mildew': [
          [1],
          ['Spraying on wheat'],
          ['triadimefon', '120-150 g/ha'],
          ['12.5% diniconazole', '525-750 g/ha']
      ],
      'Fusarium head blight': [
          [1, 3],
          ['Seed dressing'],
          ['33% WP Fungicide (carbendazim 24% + triadimefon 9%)', '200-300 g/100 kg'],
          ['Spraying on wheat'],
          ['WP', '1500-4498 g/ha']
      ],
      'Sheath blight': [
          [1, 4],
          ['Spraying on wheat'],
          ['12.5% diniconazole', '525-750 g/ha'],
          ['25% triadimefon', '300-375 g/ha'],
          ['Seed dressing'],
          ['triadimefon or triadimenol', '30 g/ 100 kg or 10 g/100 kg']
      ]
    }
  }),

  methods: {

    home () {

    },

    nextStep () {
      // Before
      this.step++;
      // End
    },

    nextStepIn () {
      // Before
      this.stepIn++;
      // End
      if (this.stepIn === 4) {
        this.$nextTick(() => {
          this.initEchart()
        })
      }
    },

    // Page V

    setImage1 (output) {
      this.hasImage1 = true
      this.image1 = output
      console.log('setImage1: %o', output)
    },

    setImage2 (output) {
      this.hasImage2 = true
      this.image2 = output
      console.log('setImage2: %o', output)
    },

    // Page VII

    initEchart () {
      this.echart = this.$echarts.init(this.$refs.echart)
      const option = {
        grid: {
          x: 0,
          x2: 0,
          y: 0,
          y2: 0,
          // top: 0,
          // left: '3%',
          // right: '4%',
          // bottom: '3%',
          containLabel: true
        },
        xAxis: {
          type: 'value',
          boundaryGap: [0, 0.01],
          axisLine: {show: false},
          axisLabel: {show: false},
          axisTick: {show: false},
          splitLine: {show: false},
        },
        yAxis: {
          type: 'category',
          data: ['R. solani', 'F. graminearum', 'B. graminis', 'P. triticina', 'P. graminis', 'P. striiformis', ]
        },
        series: [
          {
            type: 'bar',
            label: {
              formatter: 'Infected'
            },
            data: (() => {
              let data_ = []
              const values = [this.r_solani, this.f_graminearum, this.b_graminis, this.p_triticina, this.p_graminis, this.p_striiformis]
              for (let i in values) {
                data_.push({
                  value: values[i],
                  itemStyle: {
                    color: values[i] <= 0.35 ? '#888' : '#43A047'
                  },
                  label: {
                    show: values[i] > 0.35
                  }
                })
              }
              return data_
            })()
          }
        ]
      }
      this.echart.setOption(option)
      this.resizeHandler()
    },

    initListener () {
      window.addEventListener('resize', this.resizeHandler)
      this.resizeHandler()
    },

    destroyListener () {
      window.removeEventListener('resize', this.resizeHandler)
      this.resizeHandler = null
    },

    resizeHandler () {
      // this.ho = !(window.outerWidth > window.outerHeight)
      this.echart && this.echart.resize()
    },
  },

  mounted () {
  },

  destroyed () {
    // this.destroyListener()
  }
}
</script>

