            <div class="r2-type-1">N501Y</div>
            <div class="r2-type-1">E488K</div>
            <div class="r2-type-1">-</div>
          </div>
<!--          <img id="r2-output" src="" width="100%"/>-->
          <v-img :src="src" :lazy-src="src">
