                style="height: 100px"
                class="fill-height ma-0"
                align="center"
                justify="center"
            >
              <v-progress-circular
                  indeterminate
                  color="grey lighten-5"
              ></v-progress-circular>
            </v-row>
          </template>
        </v-img>
      </v-col>
      <v-col cols="6" >
        <v-card class="ml-2">
          <v-list>
            <v-subheader class="font-weight-bold" style="color: #1972D6;">
              <v-icon class="mr-1" color="primary">{{ mdiStar }}</v-icon>
              SARS-CoV-2 {{ result }}</v-subheader>
            <v-divider></v-divider>

            <v-list-item>
              <v-list-item-content>
                <v-list-item-title class="mb-1">Operator Name:</v-list-item-title>
                <v-list-item-subtitle>{{ cont.name }}</v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>
            <v-list-item>
              <v-list-item-content>
                <v-list-item-title class="mb-1">Detection Address:</v-list-item-title>
                <v-list-item-subtitle>{{ addr }}</v-list-item-subtitle>
              </v-list-item-content>
