            <v-list-item>
              <v-list-item-content>
                <v-list-item-title class="mb-1">Detection Result:</v-list-item-title>
                <v-list-item-subtitle
                  :style="{ color: danger ? 'red' : 'default' }"
                >SARS-CoV-2 {{ result }}</v-list-item-subtitle>
