<!--    </v-row>-->

    <v-row class="mb-0">
      <v-col cols="12">
        <h3>
          Preventive advice
        </h3>
      </v-col>
    </v-row>

    <v-row class="mt-0">
      <v-col :cols="ho ? 12 : 4">
        <span class="font-weight-bold" style="color: #1976d2">When purchasing frozen food</span>
        <ul>
          <li>Go to supermarket or market to buy fresh products.</li>
          <li>When buying, you can use plastic bags to cover your hands and choose frozen fresh food.</li>
          <li>Wash your hands after shopping.</li>
          <li>When shopping abroad，pay attention to the food quarantine information of the customs.</li>
        </ul>
      </v-col>

      <v-col :cols="ho ? 12 : 4">
        <span class="font-weight-bold" style="color: #1976d2">When processing frozen food</span>
        <ul>
          <li>Keep the kitchen table, tableware and container clean.</li>
          <li>Avoid touching mouth, eyes and nose with hands that have touched raw meat or aquatic products.</li>
          <li>Pay attention to the separation of raw and cooked during processing.</li>
          <li>Frozen food should be heated thoroughly.</li>
        </ul>
      </v-col>

      <v-col :cols="ho ? 12 : 4">
        <span class="font-weight-bold" style="color: #1976d2">In frozen food storage</span>
        <ul>
          <li>Raw meat in the refrigerator before freezing, it is best to split into small pieces, and packaging bags to be complete without damage.</li>
          <li>The raw and cooked food should be separated to prevent cross contamination.</li>
          <li>Frozen food should be placed to prevent the outflow of water from polluting other food.</li>
        </ul>
      </v-col>
    </v-row>

<!--    <div style="position: absolute">-->
<!--      <vue-toast ref="toast"></vue-toast>-->
<!--    </div>-->

    <div class="toast" v-show="toastShow">
      {{ toastText }}
    </div>

  </v-container>
</template>

<script>
// import 'vue-toast/dist/vue-toast.min.css'
// import VueToast from 'vue-toast'
// import { Toast } from 'mint-ui';
// import 'mint-ui/'
import { mdiStar } from '@mdi/js'

export default {
  name: "Report",

  // components: { VueToast },

  props: {
    item: {
      type: String
    }
  },

  data: () => ({
    mdiStar,
    ho: false,

    echart: null,

    toastShow: false,
    toastText: '',
    overlay: false,
    // ticket: '',
    src: '',
    src2: '',

    title: '',
    result: '',
    danger: false,

    cont: '',
    addr: '',
  }),

  mounted () {
    // console.log(this.item)
    this.cont = JSON.parse(this.item)

    let fu1, fu2, fu3, fu4, fu5, fu6
    if (this.cont.result != null) {
      const rsb = this.cont.result.split(/,|，/)
      // console.log(rsb)
      if (rsb.length === 6) {
        fu1 = (rsb[0] == '1' ? 1 : -1)
        fu2 = (rsb[1] == '1' ? 1 : -1)
        fu3 = (rsb[2] == '1' ? 1 : -1)
        fu4 = (rsb[3] == '1' ? 1 : -1)
        fu5 = (rsb[4] == '1' ? 1 : -1)
        fu6 = (rsb[5] == '1' ? 1 : -1)
      }
    }

    let sbs = []
    if (fu3 === 1) {
      sbs.push('K417N')
    }
    if (fu4 === 1) {
      sbs.push('E484K')
    }
    if (fu5 === 1) {
      sbs.push('N501Y')
    }
    if (fu6 === 1) {
      sbs.push('D614G')
    }
    let sbtext = ''
    if (sbs.length > 0) {
      sbtext = sbs.join(', ')
      this.result = ' infected'
      this.danger = true
    } else {
      sbtext = 'Not'
      this.result = ' not infected'
      this.danger = false
    }
    sbtext += ' infected'
    this.title = sbtext

    this.parse_location(this.cont.geolon, this.cont.geolat)
    if (this.cont.output != '') {
      this.src = this.BASE_URL + '/bpi/image/' + this.cont.output + '.png'
    } else {
      this.src = this.BASE_URL + '/bpi/image/' + this.cont.input2 + '.png'
    }
    this.src2 = this.BASE_URL + '/bpi/image/' + this.cont.input1 + '.png'

    this.echart = this.$echarts.init(this.$refs.echart)
    this.setHist([fu1, fu2, fu3, fu4, fu5, fu6])
    this.initListener()
  },

  methods: {
    toast (text) {
      let v = this
      v.toastText = text
      v.toastShow = true
      setTimeout(function () {
        v.toastShow = false
      }, 1500)
    },

    sendMessage () {
      let param = new FormData()
      param.append('ticket', this.cont.ticket)
      this.$post('/bpi/send', param)
          .then(r => {
            console.log(r)
            this.toast('Message Sent')
          })
    },

    initListener () {
      window.addEventListener('resize', this.resizeHandler)
      this.resizeHandler()
    },

    destroyListener () {
      window.removeEventListener('resize', this.resizeHandler)
      this.resizeHandler = null
    },

    resizeHandler () {
      this.ho = !(window.outerWidth > window.outerHeight)
      this.echart && this.echart.resize()
    },

    setHist (values) {
      const option = {
        grid: {
          x: '-2.5%',
          x2: '-2.5%',
          y: 0,
          y2: 0,
          containLabel: true
        },
        xAxis: {
          type: 'category',
          axisLine: {show: true},
          axisLabel: {show: false},
          axisTick: {show: false},
          splitLine: {show: false},
          data: ['N gene', 'E gene', 'K417N', 'E484K', 'N501Y', 'D614G']
        },
        yAxis: {
          type: 'value',
          axisLine: {show: false},
          axisLabel: {show: false},
          axisTick: {show: false},
          splitLine: {show: false},
        },
        series: [{
          barWidth: '70%',
          label: {
            show: true,
            position: 'inside',
            distance: 10,
            align: 'left',
            verticalAlign: 'middle',
            rotate: 90,
            formatter: '{b}',
            fontSize: 12,
            rich: {
              name: {
              }
            }
          },
          data: values.map((item) => {
            return {
              value: item,
              itemStyle: {
                color: item > 0 ? '#e9bfbf' : '#bfbfe9',  // #0000a9
                borderColor: item > 0 ? '#c75959' : '#5959c7'
              },
              label: {
                align: item > 0 ? 'left' : 'right',
                position: item > 0 ? 'insideBottom' : 'insideTop'
              }
            };
          }),
          type: 'bar'
        }]
      }
      this.echart.setOption(option)
    },

    parse_location (lon, lat) {
      const geocoder = new window.AMap.Geocoder({
        extensions: "base", //城市设为北京，默认：“全国”
        lang: 'en',
        radius: 1000 //范围，默认：500
      });
      geocoder.getAddress([lon, lat], (status, result) => {
        if (status === 'complete' && result.regeocode) {
          console.log(result)
          let a = result.regeocode.addressComponent
          console.log(a.city + ', ' + a.province + ', ' + a.country)
          this.addr = a.city + ', ' + a.province + ', ' + a.country
        } else {
          console.log('根据经纬度查询地址失败')
          // el.innerHTML = 'Unknown'
          // binding.value[]
          console.log('Unknown')
        }
      });
    },

    queryReport () {
      if (!this.$refs.query.validate()) {
        return
      }
      this.overlay = true
      this.$ajax('get', '/bpi/query', { ticket: this.ticket })
          .then(r => {
            this.overlay = false
            const data = r.data.data
            console.log(data)
            if (data === null) {
              this.toast('Ticket not found!')
            } else {
              let fu1, fu2, fu3, fu4, fu5, fu6
              let cont = false
              if (data.result != null) {
                const rsb = data.result.split(/,|，/)
                // console.log(rsb)
                if (rsb.length === 6) {
                  cont = true
                  fu1 = rsb[0] == '1' ? 1 : -1
                  fu2 = rsb[1] == '1' ? 1 : -1
                  fu3 = rsb[2] == '1' ? 1 : -1
                  fu4 = rsb[3] == '1' ? 1 : -1
                  fu5 = rsb[4] == '1' ? 1 : -1
                  fu6 = rsb[5] == '1' ? 1 : -1
                }
              }
              if (!cont) {
                this.toast('Wait a moment!')
              } else {
                this.setHist([fu1, fu2, fu3, fu4, fu5, fu6])
                this.show = true
                this.name = data.name
                this.time = data.intime
                this.parse_location(data.geolon, data.geolat)
                if (data.output != '') {
                  this.src = this.BASE_URL + '/bpi/image/' + data.output + '.png'
                } else {
                  this.src = this.BASE_URL + '/bpi/image/' + data.input2 + '.png'
                }
              }
            }
          })
          .catch(e => {
            this.overlay = false
            console.log(e)
            this.toast('Error get ticket')
          })
    }
  },

}
</script>

